@import url('https://fonts.googleapis.com/css?family=Abril+Fatface|Oswald');

body {
  background-image: url(./trianglify.svg);
  background-repeat: repeat-y;
  background-size: initial;
  font-family: "Oswald", sans-serif;
}

h1 {
  font-family: "Abril Fatface", serif;
  font-size: 3.5em;
  font-weight: 400;
  text-align: center;
}

p {
  font-size: 1.2em;
  line-height: 1.2em;
}

.lead {
  font-size: 1.6em;
  text-align: center;
}

.App {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: white;
  min-width: 30%;
  min-height: 80%;
  padding: 1rem 1.5rem;
  border-radius: 25px;
  -webkit-box-shadow: 0 0 25px rgb(165, 165, 165);
  box-shadow: 0 0 25px rgb(165, 165, 165);
  text-align: center;
}

@media (max-width: 1200px) {
 h1 {
   font-size: 2em;
 }
 .lead {
   font-size: 1em;
 }
}


